.container {
  background: #fceaeb;
  min-height: 100vh;
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  flex-direction: column;
}
.container h2 {
  font-size: 60px;
  color: #edb7bc;
  font-family: 'Roboto', sans-serif;
  text-transform: uppercase;
  justify-content: center;
  display: flex;
  margin: 60px 0 60px 0;
}
.container .inputWrapper {
  display: flex;
  justify-content: center;
  margin-bottom: 51px;
}
.container .inputWrapper input {
  display: flex;
  width: 573px;
  height: 55px;
  border-radius: 34px;
  border: none;
  background: #FFFFFF;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  margin-right: 23px;
  outline: none;
  padding: 18px 0 18px 26px;
  box-sizing: border-box;
  font-size: 16px;
  color: #000000;
  font-family: 'Roboto', sans-serif;
}
.container .inputWrapper input::placeholder {
  font-size: 16px;
  color: #dadab7;
  font-family: 'Roboto', sans-serif;
}
.container .wrapper {
  display: flex;
  width: 100%;
  justify-content: space-around;
  flex-direction: row;
  flex-wrap: wrap;
}
