* {
  font-family: "Robot", sans-serif;
  margin: 0 auto;
}
.colWrapper {
  display: flex;
  width: 306px;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: flex-start;
}
.colWrapper h3 {
  font-size: 27px;
  color: #edb7bc;
  font-family: 'Roboto', sans-serif;
  margin: 0 0 59px 0;
  justify-content: center;
  display: flex;
}
.colWrapper .noTask {
  font-size: 18px;
  color: #dadab7;
  font-family: 'Roboto', sans-serif;
  display: flex;
  justify-content: center;
}
