@font-face {
  font-family: "Roboto Light";
  src: url('https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap');
}
.inputButton {
  width: 92px;
  height: 55px;
  background: #edb7bc;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 34px;
  line-height: 32px;
  font-size: 27px;
  color: #ffffff;
  font-family: 'Roboto Light', sans-serif;
  border: none;
}
.but {
  width: 87px;
  height: 33px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 34px;
  border: none;
  font-size: 16px;
  color: #ffffff;
  font-family: 'Roboto Light', sans-serif;
  margin: 0;
}
.but.deleteButton {
  background: #aecdc4;
  margin-right: 8px;
}
.but.moveButton {
  background: #edb7bc;
}
