.card{
    display: flex;
    flex-wrap:wrap;
    justify-content: center;
    width: 306px;
    min-height: 163px;
    background: #FFFFFF;
    border-radius: 34px;
    margin-bottom: 32px;
}
.cardText{
    width:100%;
    margin: 34px 21px 8px 21px;
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    color:#000000;
}
.buttonContainer{
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    width: 100%;
    margin: 0 21px 15px 0;
}